.header {
  top: 0;
  position: fixed;
  align-items: center;
  justify-content: space-between;
  background-color: #06708a;
  padding: 0 10%;
  height: 10vh;
  width: 100%;
  display: flex;
  color: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
}


body {
  overflow-x: hidden;
}
